






























import Vue from "vue";

type ShapeType = "" | "circle" | "rect";

export default Vue.extend({
  props: {
    shape: {
      type: String as Vue.PropType<ShapeType>,
      default: "circle"
    },
    rotate: {
      type: String,
      default: "0"
    },
    inverse: {
      type: Boolean,
      default: false
    },
    duration: {
      type: String,
      default: "0.5s"
    }
  },
  computed: {
    style(): { [key: string]: string } {
      return {
        transform: `rotate(${this.rotate})`,
        fill: "currentColor",
        stroke: "currentColor"
      };
    },
    transition(): string {
      return `all ${this.duration}`;
    },
    black(): string {
      return this.inverse ? "white" : "";
    },
    white(): string {
      return this.inverse ? "" : "white";
    }
  }
});
